import TextField from '@mui/material/TextField';
import Autocomplete from '@mui/material/Autocomplete';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import dayjs from 'dayjs';
import React, {useEffect, useState} from 'react';
import { stocks, categories, impressions } from '../../models/contants/contants';
import IAnnouncementRequest from '../../models/announcement/announcementRequest';
import IStockLookup from '../../models/common/lookup';
import ILookup from '../../models/common/lookup';
import IAnnouncement, { AnnouncementCategory, AnnouncementImpression } from '../../models/announcement/announcement';
import AnnouncementTable from './announcementTable';
import axios from 'axios';
import { Box, LinearProgress } from '@mui/material';

const Announcements = () => {
    const [showLoading,setShowLoading] = useState<boolean>(false);
    const initAnnnouncementRequest:IAnnouncementRequest = {
        stockcodes: [],
        categories:[],
        impressions:[AnnouncementImpression.positive, AnnouncementImpression.negative],
        fromDate: new Date(),
        toDate:new Date()};
        
    const [annnouncementRequest, setAnnoncementRequest] = 
    useState<IAnnouncementRequest>(initAnnnouncementRequest);
    const [announcements, setAnnouncements] = useState<IAnnouncement[]>([]);

    const loadAnnouncements = async () => {
        setAnnouncements([]);
        setShowLoading(true);
        await axios({
            // Endpoint to send files
            url: "https://aw-backend-761436931754.asia-south1.run.app/announcements",
            method: "Post",
            headers: {
                // Add any auth token here
                authorization: "your token comes here",
            },

            // Attaching the form data
            data: annnouncementRequest,
        })
            // Handle the response from backend here
            .then((res) => {
                const data: IAnnouncement[] = res.data;
                setAnnouncements([...data]);
                setShowLoading(false);
            })
            .catch((err) => {});
    }

    // eslint-disable-next-line react-hooks/exhaustive-deps
    useEffect(() => {
        loadAnnouncements();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    },[]);

    const onFilterStocksChange = (event:any, values:ILookup[]) => {
        setAnnoncementRequest({...annnouncementRequest, stockcodes: values.map(a=> a.code)});
    } 

    const onCategoriesChange = (event:any, values:ILookup[]) => {
        setAnnoncementRequest({...annnouncementRequest, categories: values.map(a => a.code as AnnouncementCategory)});
    } 

    const onImpressionsChange = (event:any, values:ILookup[]) => {
        setAnnoncementRequest({...annnouncementRequest, impressions: values.map(a=> a.code as AnnouncementImpression)});
    } 

    const onFromDataChange = (event:any, values:any) => {
        setAnnoncementRequest({...annnouncementRequest, fromDate: event.$d});
    }

    const onToDataChange = (event:any, values:any) => {
        setAnnoncementRequest({...annnouncementRequest, toDate: event.$d});
    }


    return (
        
        <div className="card m-10">
            <div className="card-header">
                <div className="row align-items-center">
                    <div className="col">
                        <h5 className="card-header-title">Announcements</h5>
                    </div>
                    <div className="col-auto my-n3 me-n3">
                    <div className='dropdown'>
                        <button className="btn btn-sm btn-light px-3 dropdown-toggle" data-bs-target="#collapseOne" aria-controls="collapseOne" data-bs-toggle="collapse" data-bs-auto-close="outside" aria-expanded="false">
                            <span>Filter </span>
                        </button>
                    </div>
                    </div>
                </div>
            </div>

            <div className="accordion-item">

<div id="collapseOne"  className="accordion-collapse collapse" data-bs-parent="#accordionExample">
    <div className="accordion-body">
    <form>
    <div className="form-floating mb-3">
    <Autocomplete
     onChange={onFilterStocksChange}
      multiple
      id=""
      options={stocks}
      getOptionLabel={(option:IStockLookup) => option.code+' - '+option.name}
      renderInput={(params) => (
        <TextField {...params} label="Filter Stocks" placeholder="Search by scrip code or name" />
      )}
    />

</div>
        <div className="row mb-3">
  <div className="col">
  <LocalizationProvider dateAdapter={AdapterDayjs}>
      <DatePicker onChange={onFromDataChange} label="From Date" defaultValue={dayjs()}
      />
    </LocalizationProvider>
  </div>
  <div className="col">
  <LocalizationProvider dateAdapter={AdapterDayjs}>
      <DatePicker onChange={onToDataChange} label="To Date" defaultValue={dayjs()}/>
    </LocalizationProvider>
  </div>
</div>
<div className="row mb-3">
  <div className="col">
  <Autocomplete
      multiple
      id=""
      onChange={onCategoriesChange}
      options={categories}
      getOptionLabel={(option:ILookup) => option.name}
      renderInput={(params) => (
        <TextField {...params} label="Category" placeholder="Select Categories" />
      )}
    />
  </div>
  <div className="col">
  <Autocomplete
      multiple
      id=""
      onChange={onImpressionsChange}
      options={impressions}
      getOptionLabel={(option:ILookup) => option.name}
      defaultValue={[impressions[0], impressions[1]]}
      renderInput={(params) => (
        <TextField {...params} label="Impression" placeholder="Select Impressions" />
      )}
    />
  </div>
</div>
<div className="text-right">
<button onClick={loadAnnouncements} type="button" className="btn btn-outline-success">Search</button>
<button className="btn btn-outline-danger ml-7" 
                    data-bs-target="#collapseOne"  aria-controls="collapseOne" data-bs-auto-close="outside" aria-expanded="false"
                     >Cancel</button>
</div>
        
        </form>
    </div>
</div>
</div>

{ showLoading && <Box  sx={{width: '100%' }}> <LinearProgress /></Box>}

            <div className="table-responsive">
            <AnnouncementTable rows={announcements}/>
            </div>
        </div>
    )
}

export default Announcements;
