interface IAnnouncement {
    alertUniqueKey: string;
    stockCode: string;
    stockName: string;
    source: AnnouncementSource;
    exchangeCategory: string;
    subject: string;
    announcementDate: Date;
    url: string;
    category: AnnouncementCategory;
    impression: AnnouncementImpression;
    matchPhrase : string;
  }

 export enum AnnouncementSource{
    nse = "nse",
    bse = "bse"
  }

  export enum AnnouncementCategory
{
  general = "general",
  new_order = "new_order",
  bidding_win = "bidding_win",
  mou_agreement = "mou_agreement",
  subsidary_incorporation = "subsidary_incorporation",
  jv = "jv",
  financial_results = "financial_results"
}

export enum AnnouncementImpression
{
    positive = "positive",
    negative = "negative",
    nuetral = "nuetral"
}

  export default IAnnouncement;