const router = createHashRouter([
    {
      path: "/",
      element: <AppLayout />,
      //loader: AppLayout,
      children: [
        {
          path: "/",
          element: <Announcements />,
          //loader: Announcements,
        },
        {
            path: "alerts",
            element: <Alerts />,
            //loader: Announcements,
          },
      ],
    },
  ]);
  
export const App = () => <RouterProvider router={router} />

export default App;
