import * as React from 'react';
import Box from '@mui/material/Box';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TablePagination from '@mui/material/TablePagination';
import TableRow from '@mui/material/TableRow';
import TableSortLabel from '@mui/material/TableSortLabel';
import { visuallyHidden } from '@mui/utils';
import IAnnouncement, { AnnouncementImpression, AnnouncementSource } from '../../models/announcement/announcement';
import { useEffect } from 'react';
import dayjs from 'dayjs';
import { categories } from '../../models/contants/contants';

const AnnouncementTable = (props: IAnnouncementTableProps)  => {
  
    const {
        rows
      } = props;

  //const [rowsData, setRowsData] = React.useState<IAnnouncement[]>(rows);
  const [order, setOrder] = React.useState<Order>('desc');
  const [orderBy, setOrderBy] = React.useState<keyof IAnnouncement>('announcementDate');
  const [selected, setSelected] = React.useState<readonly string[]>([]);
  const [page, setPage] = React.useState(0);
  const [rowsPerPage, setRowsPerPage] = React.useState(50);
  
  useEffect(() => {
}, [rows]);

  const handleRequestSort = (
    event: React.MouseEvent<unknown>,
    property: keyof IAnnouncement
  ) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event: React.ChangeEvent<HTMLInputElement>) => {
    if (event.target.checked) {
      const newSelected = rows.map((n) => n.stockCode);
      setSelected(newSelected);
      return;
    }
    setSelected([]);
  };

  // const handleClick = (event: React.MouseEvent<unknown>, id: string) => {
  //   debugger;
  //   const selectedIndex = selected.indexOf(id);
  //   let newSelected: readonly string[] = [];

  //   if (selectedIndex === -1) {
  //     newSelected = newSelected.concat(selected, id);
  //   } else if (selectedIndex === 0) {
  //     newSelected = newSelected.concat(selected.slice(1));
  //   } else if (selectedIndex === selected.length - 1) {
  //     newSelected = newSelected.concat(selected.slice(0, -1));
  //   } else if (selectedIndex > 0) {
  //     newSelected = newSelected.concat(
  //       selected.slice(0, selectedIndex),
  //       selected.slice(selectedIndex + 1)
  //     );
  //   }
  //   setSelected(newSelected);
  // };

  const handleChangePage = (event: unknown, newPage: number) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  // Avoid a layout jump when reaching the last page with empty rows.
  const emptyRows =
    page > 0 ? Math.max(0, (1 + page) * rowsPerPage - rows.length) : 0;

  const visibleRows = React.useMemo(
    () =>
      [...rows]
        .sort(getComparator(order, orderBy))
        .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage),
    [order, orderBy, page, rowsPerPage, rows]
  );

  return (
    <div>
        <Table
          sx={{ minWidth: 750 }}
          aria-labelledby="tableTitle"
          size={'medium'}
        >
          <EnhancedTableHead
            numSelected={selected.length}
            order={order}
            orderBy={orderBy}
            onSelectAllClick={handleSelectAllClick}
            onRequestSort={handleRequestSort}
            rowCount={rows.length}
          />
          <TableBody>
            {visibleRows.map((row, index) => {
              //const isItemSelected = selected.includes(row.stockCode);
              let exchUrl = "https://upload.wikimedia.org/wikipedia/commons/thumb/5/50/BSE_logo.svg/198px-BSE_logo.svg.png";
              
              if(row.source === AnnouncementSource.nse)
                 exchUrl = "https://www.nseindia.com/assets/images/NSE_Logo.svg";

              return (
                <TableRow
                  hover
                  // onClick={(event) => handleClick(event, row.stockCode)}
                  // role="checkbox"
                  // aria-checked={isItemSelected}
                  // tabIndex={-1}
                  key={row.alertUniqueKey}
                  // selected={isItemSelected}
                  // sx={{ cursor: 'pointer' }}
                >
                  <TableCell align="left">
                    <div>
                    <div>{row.stockCode}</div>
                    <div className='text-body-secondary'>{row.stockName}</div>
                    </div>
                   
                  </TableCell>
                  <TableCell align="left">
                    {row.exchangeCategory}  
                    <img className="avatar-img ml-5" src={exchUrl} alt="..."/>
                    <div className='text-body-secondary'>{row.subject}</div>
                    </TableCell>
                  <TableCell sx={{ color:'rgb(25,135,84)', cursor: 'pointer' }} onClick={(event) => window.open(row.url,'_blank')} align="left">
                    <span>file 
                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" 
                     className="bi bi-link-45deg" viewBox="0 0 16 16">
  <path d="M4.715 6.542 3.343 7.914a3 3 0 1 0 4.243 4.243l1.828-1.829A3 3 0 0 0 8.586 5.5L8 6.086a1 1 0 0 0-.154.199 2 2 0 0 1 .861 3.337L6.88 11.45a2 2 0 1 1-2.83-2.83l.793-.792a4 4 0 0 1-.128-1.287z"/>
  <path d="M6.586 4.672A3 3 0 0 0 7.414 9.5l.775-.776a2 2 0 0 1-.896-3.346L9.12 3.55a2 2 0 1 1 2.83 2.83l-.793.792c.112.42.155.855.128 1.287l1.372-1.372a3 3 0 1 0-4.243-4.243z"/>
</svg>
                    </span>
                    </TableCell>
                  <TableCell align="left">
                  <div className='text-body-secondary'>
                    {dayjs(row.announcementDate).format("MMM D, YYYY h:mm A")}
                    </div>
                    </TableCell>
                  <TableCell align="left">
                  <div>{categories.filter(a=>a.code === row.category)[0].name}</div>
                  <small className='text-body-secondary'>{row.matchPhrase}</small>
                  </TableCell>
                  <TableCell align="left">
                  <div className='text-body-secondary'>
                  {row.impression === AnnouncementImpression.positive && <span  className="badge bg-success-subtle text-success">Positive</span>}
                  {row.impression === AnnouncementImpression.negative &&  <span className="badge bg-danger-subtle text-danger">Negative</span>}
                  {row.impression === AnnouncementImpression.nuetral && <span className="badge bg-secondary-subtle text-secondary">Nuetral</span>}
                    </div>
                  </TableCell>
                </TableRow>
              );
            })}
            {emptyRows > 0 && (
              <TableRow>
                <TableCell colSpan={6} />
              </TableRow>
            )}
          </TableBody>
        </Table>
      <TablePagination
        rowsPerPageOptions={[5, 10, 25,50,100]}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onPageChange={handleChangePage}
        onRowsPerPageChange={handleChangeRowsPerPage}
      />
    </div>
  );
}

function descendingComparator<T>(a: T, b: T, orderBy: keyof T) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

type Order = 'asc' | 'desc';

function getComparator<Key extends keyof any>(
  order: Order,
  orderBy: Key
): (
  a: { [key in Key]: number | string | Date },
  b: { [key in Key]: number | string | Date}
) => number {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

interface HeadCell {
  disablePadding: boolean;
  id: keyof IAnnouncement;
  label: string;
  numeric: boolean;
}

const headCells: readonly HeadCell[] = [
  {
    id: 'stockCode',
    numeric: false,
    disablePadding: true,
    label: 'Stock',
  },
  {
    id: 'subject',
    numeric: false,
    disablePadding: false,
    label: 'Subject',
  },
  {
    id: 'url',
    numeric: false,
    disablePadding: false,
    label: 'URL',
  },
  {
    id: 'announcementDate',
    numeric: false,
    disablePadding: false,
    label: 'Date',
  },
  {
    id: 'category',
    numeric: false,
    disablePadding: false,
    label: 'Category',
  },
  {
    id: 'impression',
    numeric: false,
    disablePadding: false,
    label: 'Impression',
  },
];

interface EnhancedTableProps {
  numSelected: number;
  onRequestSort: (
    event: React.MouseEvent<unknown>,
    property: keyof IAnnouncement
  ) => void;
  onSelectAllClick: (event: React.ChangeEvent<HTMLInputElement>) => void;
  order: Order;
  orderBy: string;
  rowCount: number;
}

function EnhancedTableHead(props: EnhancedTableProps) {
  const {
    order,
    orderBy,
    onRequestSort,
  } = props;
  const createSortHandler =
    (property: keyof IAnnouncement) => (event: React.MouseEvent<unknown>) => {
      onRequestSort(event, property);
    };

  return (
    <TableHead>
      <TableRow>
        {headCells.map((headCell) => (
          <TableCell
            key={headCell.id}
            align={'left'}
            padding={headCell.disablePadding ? 'none' : 'normal'}
            sortDirection={orderBy === headCell.id ? order : false}
          >
            <TableSortLabel
              active={orderBy === headCell.id}
              direction={orderBy === headCell.id ? order : 'asc'}
              onClick={createSortHandler(headCell.id)}
            >
              {headCell.label}
              {orderBy === headCell.id ? (
                <Box component="span" sx={visuallyHidden}>
                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                </Box>
              ) : null}
            </TableSortLabel>
          </TableCell>
        ))}
      </TableRow>
    </TableHead>
  );
}

interface IAnnouncementTableProps{
    rows:IAnnouncement[]
}

export default AnnouncementTable;
